const { spawn } = require("child_process");

const log = require("./m-log.js");

import isDev from "electron-is-dev";
const isBOX = require("../config.json").BOX;

// const Gap = require('../gap/gap.js');
// const log = require('./m-log.js')

class Launcher {
  constructor(app, mainWindow, event, data, cb) {
    this.mainWindow = mainWindow;
    this.mChildProcess = null;
    this.data = data;
    this.event = event;
    this.app = app;
    this.cb = cb;

    // process.kill(-data.killPID, 0)
    if (data.killPID) {
      setTimeout(() => {
        this.spawn();
      }, 5000);

      console.log("kill killPID");

      process.kill(-data.killPID, 0);
      return;
    }

    this.spawn();
  }

  spawn() {
    this.streaming = false;
    setTimeout(() => {
      if (this.mChildProcess && this.streaming === false) {
        this.mainWindow.webContents.send("error", { code: 203 });
        this.close(203);
      }
    }, 40000);

    if (process.platform === "win32") {
      let options = [
        this.data.ip,
        this.data.port,
        "-t",
        this.data.user.token,
        "-a",
        this.data.userAgent
      ];

      if (this.data.deepLink) options.push("--secondary");

      if (this.data.userOptions.fullscreen) options.push("-f");

      if (
        this.data.userOptions.bandwidth === 0 ||
        this.data.userOptions.quality_auto
      )
        options.push("-d");
      else options.push("-b", this.data.userOptions.bandwidth);

      if (!this.data.userOptions.usb) options.push("--no-usb");

      if (this.data.userOptions.resolution)
        options.push("--set-resolution-auto");

      if (this.data.userOptions.framerate) options.push("-s");

      if (this.data.userOptions.hevc) options.push("--hevc");

      if (this.data.userOptions.color) options.push("--situ");

      if (this.data.userOptions.enable_mic) options.push("--enable-mic");

      if (this.data.userOptions.shadowvision === -1)
        options.push("--no-update");
      else options.push("--branch", this.data.userOptions.shadowvision);

      if (this.data.userOptions.tcpaudio || this.data.userOptions.tcpinput)
        options.push("--use-cursor-tcp");

      if (this.data.userOptions.tcpaudio) options.push("--use-audio-tcp");

      if (this.data.userOptions.tcpinput) options.push("--use-input-tcp");

      console.log(options);

      const windowsFolder =
        process.arch === "x64" ? "/native/windows/x64" : "/native/windows/ia32";

      if (isDev) {
        this.mChildProcess = spawn("Shadow.exe", options, {
          cwd: __dirname.replace("controllers", "") + "native/windows/x64",
          detached: true,
          stdio: ["ignore", "ignore", "ignore", "ipc"]
        });
      } else {
        this.mChildProcess = spawn("Shadow.exe", options, {
          cwd:
            __dirname.replace("app.asar", "app.asar.unpacked") + windowsFolder,
          detached: true,
          stdio: ["ignore", "ignore", "ignore", "ipc"]
        });
      }
    } else if (process.platform === "darwin") {
      console.log(this.data);

      let options = [];

      if (this.data.deepLink) {
        options = [
          "--ip",
          this.data.ip,
          "--port",
          this.data.port,
          "--token",
          this.data.token,
          "--agent",
          this.data.userAgent,
          "--secondary"
        ];
      } else {
        options = [
          "--ip",
          this.data.ip,
          "--port",
          this.data.port,
          "--token",
          this.data.user.token,
          "--agent",
          this.data.userAgent
        ];
      }

      if (this.data.userOptions.hevc) options.push("--hevc");

      if (
        this.data.userOptions.bandwidth === 0 ||
        this.data.userOptions.quality_auto
      )
        options.push("-d");
      else options.push("-b", this.data.userOptions.bandwidth);

      if (this.data.userOptions.resolution)
        options.push("--set-resolution-auto");

      if (this.data.userOptions.enable_mic) options.push("--enable-mic");

      if (this.data.userOptions.color) options.push("--situ");

      if (this.data.userOptions.framerate) options.push("-s");

      if (this.data.userOptions.shadowvision === -1)
        options.push("--no-update");
      else options.push("--branch", this.data.userOptions.shadowvision);

      if (this.data.userOptions.tcpaudio || this.data.userOptions.tcpinput)
        options.push("--use-cursor-tcp");

      if (this.data.userOptions.tcpaudio) options.push("--use-audio-tcp");

      if (this.data.userOptions.tcpinput) options.push("--use-input-tcp");

      if (this.data.userOptions.fullscreen) options.push("-f");

      console.log(options);
      console.log(__dirname.replace("controllers", "") + "native/mac");

      if (isDev) {
        this.mChildProcess = spawn(
          "Shadow.app/Contents/MacOS/Shadow",
          options,
          {
            cwd: __dirname.replace("controllers", "") + "native/mac",
            detached: true,
            stdio: ["ignore", "ignore", "ignore", "ipc"]
          }
        );
      } else {
        this.mChildProcess = spawn(
          "Shadow.app/Contents/MacOS/Shadow",
          options,
          {
            cwd:
              __dirname.replace("app.asar", "app.asar.unpacked") +
              "/native/mac",
            detached: true,
            stdio: ["ignore", "ignore", "ignore", "ipc"]
          }
        );
      }
    } else if (process.platform === "linux") {
      const linuxFolder = "/native/linux";

      let path = __dirname.replace("/controllers", "") + linuxFolder;

      if (!isDev)
        path = __dirname.replace("app.asar", "app.asar.unpacked") + linuxFolder;

      let options = [
        "--server",
        this.data.ip,
        "--port-offset",
        this.data.port,
        "--token",
        this.data.user.token,
        "-l",
        this.data.user.email,
        "--datadir",
        isBOX ? "/usr/share/shadow" : "data"
      ];

      if (this.data.userOptions.hevc) options.push("--hevc");

      if (
        this.data.userOptions.bandwidth === 0 ||
        this.data.userOptions.quality_auto
      ) {
        options.push("--autobitrate", "yes");
      } else {
        options.push("--bitrate", this.data.userOptions.bandwidth);
      }

      if (!isBOX && !this.data.userOptions.fullscreen) options.push("--window");

      console.log(options);

      log.error(
        __dirname.replace("app.asar", "app.asar.unpacked") + linuxFolder
      );
      console.log(
        "dev dir",
        __dirname.replace("/controllers", "") + linuxFolder
      );
      console.log(
        "release dir",
        __dirname.replace("app.asar", "app.asar.unpacked") + linuxFolder
      );
      if (isBOX) {
        if (isDev) {
          this.mChildProcess = spawn("/usr/bin/ClientSDL", options, {
            detached: true,
            stdio: ["ignore", "ignore", "ignore", "ipc"]
          }).on("error", function(err) {
            throw err;
          });
        } else {
          this.mChildProcess = spawn("/usr/bin/ClientSDL", options, {
            detached: true,
            stdio: ["ignore", "ignore", "ignore", "ipc"]
          }).on("error", function(err) {
            throw err;
          });
        }
      } else {
        if (isDev) {
          this.mChildProcess = spawn("./ClientSDL", options, {
            cwd: __dirname.replace("/controllers", "") + linuxFolder,
            detached: true,
            stdio: ["ignore", "ignore", "ignore", "ipc"]
          }).on("error", function(err) {
            throw err;
          });
        } else {
          this.mChildProcess = spawn("./ClientSDL", options, {
            cwd:
              __dirname.replace("app.asar", "app.asar.unpacked") + linuxFolder,
            detached: true,
            stdio: ["ignore", "ignore", "ignore", "ipc"]
          }).on("error", function(err) {
            throw err;
          });
        }
      }
    } else {
      return log.error("os-not-supported");
    }

    if (this.mChildProcess) {
      return this.setIpc();
    } else {
      return "child-not-created";
    }
  }

  close() {
    if (this.mChildProcess) {
      console.log("close child");

      this.mChildProcess.kill("SIGINT");
      this.mChildProcess = null;

      console.log("killed this.mChildProcess");
    }

    this.pid = null;
  }

  hardClose() {
    if (this.mChildProcess) {
      console.log("kill child");
      this.mChildProcess.kill("SIGKILL");
      this.mChildProcess = null;
      this.pid = null;
    }

    if (this.pid) process.kill(-this.pid, 0);
  }

  setIpc() {
    this.pid = this.mChildProcess.pid;
    this.mainWindow.webContents.send("data", {
      state: "started",
      pid: this.mChildProcess.pid
    });

    this.mChildProcess.on("exit", (code, signal) => {
      console.log(
        `child process exited with code ${code} and/or signal ${signal}`
      );
      log.error(
        `child process exited with code ${code} and/or signal ${signal}`
      );
      if (this.mChildProcess && this.mChildProcess.connected) {
        this.mChildProcess.disconnect();
      }
    });

    this.mChildProcess.on("close", (code, signal) => {
      console.log(
        `child process closed with code ${code} and/or signal ${signal}`
      );
      log.error(
        `child process closed with code ${code} and/or signal ${signal}`
      );

      if (this.cb) this.cb();

      if (this.mChildProcess && this.mChildProcess.connected) {
        this.mChildProcess.disconnect();
      }

      this.mainWindow.show();
      this.mainWindow.focus();

      this.mChildProcess = null;
      this.pid = null;

      if (this.linuxCode) {
        this.mainWindow.webContents.send("error", { code: this.linuxCode });
        this.linuxCode = undefined;
        return;
      }

      if (this.streaming === false && !code)
        return this.mainWindow.webContents.send("error", { code: 203 });

      if (code) return this.mainWindow.webContents.send("error", { code });

      // if(this.data.deepLink)
      //     this.mainWindow.webContents.send('share-closed')
      // else
      this.mainWindow.webContents.send("data", { state: `closed` });
    });

    this.mChildProcess.on("message", data => {
      // data { channel: 'video', status: 'started' }
      // data { channel: 'video', status: 'stopped' }

      console.log(data);
      // log.error( data)

      switch (data["channel"]) {
        case "comchan": {
          if (data.status === "connected") {
            console.log("--->>> comchan connected");

            if (this.data.rebootStream) {
              console.log("reboot stream asked");
              this.sendCmd({
                cmd: "config",
                config: JSON.stringify(this.config)
              });
              this.data.rebootStream = false;
            }
          }

          if (data.status === "finish") {
            this.linuxCode = data.code;

            if (this.linuxCode)
              this.mainWindow.webContents.send("error", { code: data.code });

            console.log('event comchan "finish" : code ->' + data.code);

            setTimeout(() => {
              this.hardClose();
            }, 5000);
          }

          break;
        }

        case "video": {
          if (data.status === "started") {
            this.streaming = true;
            /* if(!isDev && process.platform === 'darwin')
                            this.app.dock.hide()*/
            this.mainWindow.hide();

            this.mainWindow.webContents.send("data", {
              state: "running"
            });
          } else if (data.status === "stopped") {
            // this.mainWindow.webContents.send('data', {state: `closed`})
            if (!isDev && process.platform === "darwin") this.app.dock.show();
            this.mainWindow.show();
          } else if (data.status === "notification") {
            this.mainWindow.webContents.send("data", {
              state: `notification`,
              code: data.reason
            });
          }

          break;
        }

        case "ctrlchan": {
          if (data.status === "channels-starting") {
            console.log("--->>> controlchan connected");

            if (this.data.rebootStream) {
              console.log("reboot stream asked");
              this.sendCmd({ cmd: "reboot-stream" });
              this.data.rebootStream = false;
            }
          }

          break;
        }

        case "VmProxy": {
          if (data.status === "reboot") {
            // this.mainWindow.show()
            this.mainWindow.webContents.send("data", {
              state: `reboot`
            });
          } else if (data.status === "started") {
            if (this.data.restartWindows) {
              console.log("restart windows asked");
              this.sendCmd({ cmd: "restart-windows" });
              this.data.restartWindows = false;
            }
          } else if (data.reason === "windows-shutdown") {
            console.log("windows shutdown");
            this.mainWindow.webContents.send("data", { state: `shutdown` });
          }
          break;
        }

        default: {
          break;
        }
      }

      // ipc.on("reboot-stream", (event, arg) => {
      //     log.info("tray IPC : reboot-stream " + arg);
      //     rendererCom.sendCmd({ cmd: 'reboot-stream' });
      //   })

      //   ipc.on("stop-windows", (event, arg) => {
      //     log.info("tray IPC : stop-windows " + arg);
      //     callGapStop();
      //   })

      //   ipc.on("restart-windows", (event, arg) => {
      //     log.info("tray IPC : restart-windows " + arg);
      //     rendererCom.sendCmd({ cmd: 'restart-windows' });
      //   })

      /* Handle command here */
      switch (data["cmd"]) {
        case "window-status":
          console.log(
            "window is " + (data["visible"] ? "visible" : "not visible")
          );
          return `renderer-connected -> ${data["visible"]}`;
        case "heartbeat":
          this.sendCmd({ resp: "heartbeat" });
          return "heartbeat";
        default:
          break;
      }

      /* Handle response here */
      switch (data["resp"]) {
        case "reboot-streaming":
          console.log("reboot-streaming response");
          break;
        case "restart-windows":
          console.log("restart-streaming response");
          break;
        default:
          break;
      }
    });
  }

  sendCmd(toSend) {
    // restart-windows
    // reboot-stream
    if (this.mChildProcess && this.mChildProcess.connected) {
      if (!this.mChildProcess.send(toSend)) {
        return "cmd-not-send";
      }
    }
  }

  /** GETTER */

  // childInProcess() {
  //     return this.mChildProcess != null ? true : false
  // }

  /** CLEANNER */

  // clear() {
  //     delete this.user
  //     delete this.vm

  //     if (!this.mChildProcess)
  //         return
  //     if (this.mChildProcess.connected) {
  //         this.mChildProcess.disconnect()
  //     }
  //     this.mChildProcess = null
  // }

  // hardClose() {
  //     if (this.mChildProcess) {
  //         this.mChildProcess.kill('SIGTERM')
  //         this.mChildProcess = null
  //     }
  // }
}

export default Launcher;



