import { shell, BrowserWindow, Menu, ipcMain } from "electron";

import isDev from "electron-is-dev";
import { autoUpdater } from "electron-updater";

const config = require("../config.json");

import log from "./m-log";

autoUpdater.logger = log;

class WindowApp {
  create(deepToken) {
    this.deepToken = deepToken;

    if (this.deepToken) log.error("deepToken", this.deepToken);

    this.contextWindow = new BrowserWindow({
      frame: false,
      show: false,
      transparent: false,
      movable: true,
      closable: true,
      backgroundColor: "#282b30",
      width: 300,
      height: 240,
      minWidth: 300,
      minHeight: 240,
      resizable: isDev,
      disableAutoHideCursor: true,
      experimentalFeatures: true,
      experimentalCanvasFeatures: true,
      scrollBounce: true,
      focusable: true,
      webPreferences: {
        webSecurity: false
      }
      // skipTaskbar: true
    });

    if (config.BOX && !isDev) this.contextWindow.setFullScreen(true);

    // Create the Application's main menu
    var template = [
      {
        label: "Application",
        submenu: [
          {
            label: "About Application",
            selector: "orderFrontStandardAboutPanel:"
          },
          { type: "separator" },
          {
            role: "quit"
          }
        ]
      },
      {
        label: "Edit",
        submenu: [
          { label: "Undo", accelerator: "CmdOrCtrl+Z", selector: "undo:" },
          {
            label: "Redo",
            accelerator: "Shift+CmdOrCtrl+Z",
            selector: "redo:"
          },
          { type: "separator" },
          { label: "Cut", accelerator: "CmdOrCtrl+X", selector: "cut:" },
          { label: "Copy", accelerator: "CmdOrCtrl+C", selector: "copy:" },
          { label: "Paste", accelerator: "CmdOrCtrl+V", selector: "paste:" },
          {
            label: "Select All",
            accelerator: "CmdOrCtrl+A",
            selector: "selectAll:"
          }
        ]
      }
    ];

    Menu.setApplicationMenu(Menu.buildFromTemplate(template));

    var handleRedirect = (e, url) => {
      if (url != this.contextWindow.webContents.getURL()) {
        e.preventDefault();
        shell.openExternal(url);
      }
    };

    this.contextWindow.webContents.on("will-navigate", handleRedirect);
    // this.contextWindow.webContents.on('new-window', handleRedirect)

    this.contextWindow.on("new-window", (event, url) => {
      console.log("new-window");
      event.preventDefault();
      shell.openExternal(url);
    });

    this.contextWindow.on("closed", () => {
      this.contextWindow = null;
    });

    if (isDev) this.contextWindow.openDevTools();

    this.contextWindow.once("ready-to-show", () => {
      console.log("ready-to-show");
      // this.show();
      //            this.checkForUpdates()
    });

    this.contextWindow.webContents.on("did-finish-load", () => {
      console.log("did-finish-load");
      if (!this.contextWindow) {
        throw new Error("this.contextWindow is not defined");
      }

      this.contextWindow.webContents.setZoomFactor(1);
      this.contextWindow.webContents.setVisualZoomLevelLimits(1, 1);
      this.contextWindow.webContents.setLayoutZoomLevelLimits(0, 0);
      // this.contextWindow.show()
      // this.contextWindow.focus()
    });

    console.log("new WindowsApp");

    if (isDev)
      this.contextWindow.loadURL(`file://${__dirname}/../app.html?updater`);
    else this.contextWindow.loadURL(`file://${__dirname}/app.html?updater`);
  }

  openDevTools() {
    console.log("devtool");
    this.contextWindow.openDevTools();
  }

  // show() {
  //   this.contextWindow.show();
  //   this.contextWindow.focus();
  // }

  switchToApp() {
    if (!config.BOX || isDev) {
      const position = this.contextWindow.getPosition();
      // this.contextWindow.setPosition(position[0] + 200 - 500, position[1] + 200 - 300)
      this.contextWindow.setSize(1000, 600);
      this.contextWindow.setMinimumSize(1000, 600);
      this.contextWindow.center();
    }
  }

  sendMessage(name, data) {
    this.contextWindow.webContents.send(name, data);
  }

  checkForUpdates(state) {
    this.state = state;
    let serverUrl = config.SERVER_URL;

    console.log("checkForUpdates : ", state);

    if (process.platform == "win32") {
      if (process.arch == "x64") {
        serverUrl += "/x64";
      } else {
        serverUrl += "/x86";
      }
    }

    autoUpdater.setFeedURL(serverUrl);
    autoUpdater.autoDownload = false;

    autoUpdater.on("checking-for-update", () => {
      console.log("Checking for update...");
      log.error("Checking for update...");
      this.contextWindow.webContents.send("starting", {
        state: "checking"
      });
    });

    autoUpdater.on("update-available", info => {
      console.log("Update available.");
      log.error("Update available.");
      console.log("Update available.", info);

      if (!this.state)
        setTimeout(() => {
          this.contextWindow.webContents.send("starting", {
            state: "update-available"
          });
        }, 1000);
      else {
        this.contextWindow.webContents.send("check-for-update", {
          status: "update-available"
        });
      }
    });

    autoUpdater.on("update-not-available", info => {
      console.log("Update not available.");

      autoUpdater.removeAllListeners();

      if (!this.state) {
        setTimeout(() => {
          this.contextWindow.webContents.send("starting", {
            state: "starting",
            deep: this.deepToken
          });
        }, 1000);
      } else {
        this.contextWindow.webContents.send("check-for-update", {
          status: "no-update"
        });
      }
    });

    autoUpdater.on("error", err => {
      console.log("Error in auto-updater.");
      log.error("Error in auto-updater.");

      autoUpdater.removeAllListeners();

      if (!this.state) {
        this.contextWindow.webContents.send("starting", {
          state: "error"
        });
      } else {
        this.contextWindow.webContents.send("check-for-update", {
          status: "no-update"
        });
      }
    });

    autoUpdater.on("download-progress", progressObj => {
      let log_message = "Download speed: " + progressObj.bytesPerSecond;
      log_message = log_message + " - Downloaded " + progressObj.percent + "%";
      log_message =
        log_message +
        " (" +
        progressObj.transferred +
        "/" +
        progressObj.total +
        ")";
      console.log(log_message);
      log.error(log_message);

      this.contextWindow.webContents.send("starting", {
        state: "updating",
        percent: progressObj.percent
      });
    });

    autoUpdater.on("update-downloaded", (event, releaseNotes, releaseName) => {
      this.contextWindow.webContents.send("starting", {
        state: "restart"
      });

      setTimeout(() => {
        autoUpdater.quitAndInstall();
      }, 2000);
    });

    if (config.BOX) {
      autoUpdater.removeAllListeners();
      this.contextWindow.webContents.send("starting", {
        state: "starting"
      });
    } else {
      autoUpdater.checkForUpdates();
    }
  }

  update() {
    autoUpdater.downloadUpdate();
  }

  close() {
    this.contextWindow.close();
  }
}

const windowApp = new WindowApp();

export default windowApp;



