const { spawn } = require("child_process");
import log from "./m-log";

import isDev from "electron-is-dev";

// const Gap = require('../gap/gap.js');
// const log = require('./m-log.js')

class Usb {
  spawn(webContents) {
    let path;
    if (isDev)
      path = `${__dirname.replace("controllers", "") +
        "\\native\\windows\\x64\\"}`;
    else
      path = `${__dirname.replace(
        "app.asar",
        "app.asar.unpacked"
      )}\\native\\windows\\x64\\`;

    // path = `msiexec.exe /i ${__dirname.replace("controllers", "") +
    //   "\\native\\windows\\x64\\UsbDk_1.0.19_x64.msi"}`;

    log.error(path);
    this.mChildProcess = spawn("cmd", ["/S /C " + "UsbDk_1.0.19_x64.msi"], {
      detached: true,
      cwd: path,
      env: process.env,
      stdio: ["ignore", "ignore", "ignore", "ipc"]
    });

    this.mChildProcess.on("exit", (code, signal) => {
      console.log(
        `child process exited with code ${code} and/or signal ${signal}`
      );
      if (this.mChildProcess && this.mChildProcess.connected) {
        this.mChildProcess.disconnect();
      }
      webContents.send("usb-driver", { found: !code ? true : false });
    });

    this.mChildProcess.on("close", (code, signal) => {
      console.log(
        `usb driver process closed with code ${code} and/or signal ${signal}`
      );
      if (this.mChildProcess && this.mChildProcess.connected) {
        this.mChildProcess.disconnect();

        webContents.send("usb-driver", { found: !code ? true : false });
      }
    });
  }
}

const usb = new Usb();
export default usb;



