import wifi from 'node-wifi';
import WindowApp from '../controllers/WindowApp';
import sendToClient from './sendToClient';

wifi.init({
  iface: null, // network interface, choose a random wifi interface if set to null
});

export function scanWifi() {
  wifi.scan((error, networks) => {
    if (error) {
      sendToClient('scan-wifi-error', { error });
    } else {
      console.log(networks.map(n => n.ssid));
      sendToClient('scan-wifi-success', {
        networks,
      });
      /*
        networks = [
            {
              ssid: '...',
              bssid: '...',
              mac: '...', // equals to bssid (for retrocompatibility)
              channel: <number>,
              frequency: <number>, // in MHz
              signal_level: <number>, // in dB
              security: 'WPA WPA2' // format depending on locale for open networks in Windows
              security_flags: '...' // encryption protocols (format currently depending of the OS)
              mode: '...' // network mode like Infra (format currently depending of the OS)
            },
            ...
        ];
        */
    }
  });
}

export async function checkNetwork() {
  console.log('getCurrentConnections');
  const currentConnections = await wifi.getCurrentConnections();
  // console.log('currentConnections', currentConnections);

  WindowApp.contextWindow.webContents.send('network-status', {
    currentConnections,
  });
}

export default {};



