const log = require('electron-log')

function mLog() {
    const DEBUG_TAG = "Shadow electron app: "
    const logLevel = ['error', 'warn', 'info', 'verbose', 'debug', 'silly'];
    /**
     * all log level possiblie
     * error, warn, info, verbose, debug, silly
     */

    /* write into log file */
    log.transports.file.level = false;
    log.transports.file.format = '[{h}:{i}:{s}:{ms}] [{level}] - {text}';

    /* write into console */
    log.transports.console.level = 'debug';
    log.transports.console.format = '[{h}:{i}:{s}:{ms}] [{level}] - {text}';

    /**
     * this method is for display also console log
     * 'limit' is the minimum level accepted
     */
    this.enableDebug = function (limit) {
        if (limit && logLevel.includes(limit)) {
            log.transports.console.level = limit;
        } else {
            log.transports.console.level = 'debug';
        }
    }

    /**
     * this method enable displaying for log file
     * 'limit' is the minimum level accepted
     */
    this.enableRelease = function (limit) {
        if (limit && logLevel.includes(limit)) {
            log.transports.file.level = limit;
        } else {
            log.transports.file.level = 'info';
        }
    }

    this.desableRelease = function () {
        log.transports.file.level = false;
    }

    this.desableDebug = function () {
        log.transports.console.level = false;
    }

    this.error = function (msg) {
        log.error(msg);
    }

    this.warn = function (msg) {
        log.warn(msg);
    }

    this.info = function (msg) {
        log.info(msg);
    }

    this.verbose = function (msg) {
        log.verbose(msg);
    }

    this.debug = function (msg) {
        log.debug(msg);
    }

    this.silly = function (msg) {
        log.silly(msg);
    }

}

module.exports = new mLog();



