ipcMain.on('update-box', async (event, data) => {
  console.log('update-box');
  let dir;

  if (data.state === 'switch') {
    console.log('update-box switch to prod');
    dir = exec(
      '/usr/bin/update-shadow --graphic --force --imgdir /usr/share/shadow/images/fr_FR --force --reboot prod',
      (err, stdout, stderr) => {
        if (err) {
          console.log(err);
          log.error(err);
        }
        log.error(stdout);
      },
    );
  } else {
    console.log('update-box update');
    dir = exec(
      '/usr/bin/update-shadow --graphic --imgdir /usr/share/shadow/images/fr_FR --reboot --fast insider',
      (err, stdout, stderr) => {
        if (err) {
          console.log(err);
          log.error(err);
        }
        log.error(stdout);
      },
    );
  }

  dir.on('exit', code => {
    log.error(code);
    console.log(code);
  });
});

ipcMain.on('check-usb-driver', async (event, data) => {
  if (process.platform === 'win32' && process.arch !== 'ia32') {
    let Registry = require('winreg'),
      regKey = new Registry({
        hive: Registry.HKLM,
        key:
          '\\SOFTWARE\\Microsoft\\Windows\\CurrentVersion\\Uninstall\\{446D7CEA-0B07-44FF-8981-37985CA96C41}',
      });

    regKey.values((err, items /* array of RegistryItem */) => {
      if (err) {
        Usb.spawn(WindowApp.contextWindow.webContents);
      } else {
        WindowApp.sendMessage('usb-driver', { found: true });
      }
    });
  } else WindowApp.sendMessage('usb-driver', { found: true });
});

let deeplinkingUrl;

const shouldQuit = app.makeSingleInstance((commandLine, workingDirectory) => {
  log.error(
    'Someone tried to run a second instance, we should focus our window',
  );

  // Protocol handler for win32
  // argv: An array of the second instance’s (command line / deep linked) arguments
  // if (process.platform == 'win32') {
  //   // Keep only command line / deep linked arguments
  //   deeplinkingUrl = process.argv.slice(1);
  // }

  // Someone tried to run a second instance, we should focus our window.
  if (WindowApp.contextWindow) {
    if (WindowApp.contextWindow.isMinimized())
      WindowApp.contextWindow.restore();
    WindowApp.contextWindow.focus();
  }
});

if (shouldQuit) {
  app.quit();
  process.exit();
}

