ipcMain.on('speedtest', async (event, data) => {
  const test = speedTest({
    maxTime: 5000,
    serverId: 1,
    serversUrl: 'http://speedtest.shadow.guru/servers.xml',
    // serversUrl: 'http://speedtest.ams.shadow.guru/servers.xml'
  });

  test.on('downloadspeedprogress', speed => {
    console.log(
      'Download speed (in progress):',
      (speed * 125).toFixed(2),
      'KB/s',
    );
  });

  test.on('downloadprogress', progress => {
    console.log('Download Progress: ', progress);
    WindowApp.contextWindow.webContents.send('current-speedtest-progress', {
      progress,
    });
  });

  test.on('data', data => {
    console.log(data);
    console.log(`Download Speed: ${data.speeds.download}`);
    WindowApp.contextWindow.webContents.send('speedtest', {
      speed: Math.round(data.speeds.download),
    });
  });

  test.on('error', err => {
    WindowApp.contextWindow.webContents.send('speedtest-error');
    console.error(err);
  });
});

