let socket = null;
let screen = '';
let timer = '';

ipcMain.on('getScreenshot', async (event, { ip, port, token, component }) => {
  console.log('Im in', ip, port, parseInt(port, 10) + 7005, token, component);
  if (!socket && token !== undefined) {
    console.log('Try connection');
    socket = tls.connect(
      {
        host: ip,
        port: parseInt(port, 10) + 7001,
        rejectUnauthorized: false,
      },
      err => {
        if (err) console.log('ERROR: ', err);
        else console.log('TLS Connected');
      },
    );
  }
  if (token !== undefined) {
    try {
      // for (let i = 0; i <= 3; i++)
      // socket.write(Buffer.from(JSON.stringify({ cmd: 'reboot', token })));
      console.log('TRY TO WRITE');
      socket.write(Buffer.from(JSON.stringify({ cmd: 'screen', token })));
      timer = setTimeout(() => {
        WindowApp.contextWindow.webContents.send('screenshot', {
          screen: `data:image/png;base64,null`,
          type: 'error',
          ocapture_up: true,
        });
      }, 5000);
    } catch (e) {
      console.log('ERROR::', e);
      socket = tls.connect(
        {
          host: ip,
          port: parseInt(port, 10) + 7001,
          rejectUnauthorized: false,
        },
        err => {
          if (err) console.log('ERROR: ', err);
          else console.log('TLS Connected');
        },
      );
      // for (let i = 0; i <= 3; i++)
      // socket.write(Buffer.from(JSON.stringify({ cmd: 'reboot', token })));
      console.log('TRY TO WRITE');

      socket.write(Buffer.from(JSON.stringify({ cmd: 'screen', token })));
    }
    socket.on('data', result => {
      clearTimeout(timer);
      screen += result.toString();
      // console.log(result.toString());

      if (result.toString().length < 1024) {
        console.log(
          screen[screen.length - 1],
          screen.length,
          screen,
          'data',
          component,
        );
        if (screen[0] === '{' && screen[screen.length - 1] === '}') {
          try {
            screen = JSON.parse(screen);
          } catch (e) {
            console.log('ERROR ON JSON SCREEN');
          }
          console.log('I send screen', screen);
          // if (screen.ocapture_up) {
          //   WindowApp.contextWindow.webContents.send('start-shadow-screen');
          // }
          if (!screen.error && !screen.err) {
            WindowApp.contextWindow.webContents.send('screenshot', {
              screen: `data:image/png;base64,${screen.screen}`,
              type: screen.screen,
              ocapture_up: screen.ocapture_up,
            });
          } else {
            WindowApp.contextWindow.webContents.send('screenshot', {
              screen: `data:image/png;base64,${screen.screen}`,
              type: screen.screen,
              ocapture_up: screen.ocapture_up,
              error: screen.error || screen.err,
            });
          }
        }
        screen = '';
        socket.removeAllListeners('data');
      }
    });
  }
});

