function parseDeepLink() {
  const url = process.argv[1].split('//')[1];

  if (url) return url.split('/');
}

app.on('ready', async () => {
  if (
    process.env.NODE_ENV === 'development' ||
    process.env.DEBUG_PROD === 'true'
  ) {
    await installExtensions();
  }

  // if (!isDev && process.platform === 'win32' && process.argv.length > 1) {

  //   console.log('deeplink', process.argv.length)
  //   // Keep only command line / deep linked arguments
  //   deeplinkingUrl = parseDeepLink()[0];
  // }

  WindowApp.create();

  electron.powerMonitor.on('suspend', () => {
    console.log('The system is going to sleep', launcher);
    // The system is going to sleep
    if (launcher) {
      launcher.close();
      launcher = null;
    }
  });

  electron.powerMonitor.on('lock-screen', () => {
    console.log('The system is about to lock the screen.', launcher);
    // The system is about to lock the screen.
    if (launcher) {
      launcher.close();
      launcher = null;
    }
  });
});

